    }

    #LessonTopics > .wrapper > .LessonTopics_Container > header > p{
        font-size: 30px;
    }


    #LessonTopics > .wrapper > .LessonTopics_Container > .LessonTopics_Content > .Topic > .Left_Side > .Title{
        font-size: 22px;
    }
    #LessonTopics > .wrapper > .LessonTopics_Container > .LessonTopics_Content > .Topic > .Left_Side > .Description{
        font-size: 16px;
    }

    #LessonTopics > .wrapper > .LessonTopics_Container > .LessonTopics_Content > .Topic > .Right_Side > .Image{
        width: 115px;
        height: 115px;
    }
    
    #LessonTopics > .wrapper > .LessonTopics_Container > .LessonTopics_Content > .Topic > .Right_Side > .Image > img{
        width: 65px;
        height: 65px;
    }
}

