    #LessonTopics > .wrapper > .LessonTopics_Container > header > .Back > *{
        width: 20px;
        height: 20px;
    }

    #LessonTopics > .wrapper > .LessonTopics_Container > header > p{
        font-size: 20px;
    }

    #LessonTopics > .wrapper > .LessonTopics_Container > .LessonTopics_Content > .Topic > .Left_Side{
        width: 80%;
    }

    #LessonTopics > .wrapper > .LessonTopics_Container > .LessonTopics_Content > .Topic > .Right_Side{
        width: 20%;
    }

